@import url('https://fonts.googleapis.com/css2?family=Albert+Sans:ital,wght@0,100..900;1,100..900&display=swap');

.wrapper{
    width:100%;
    height:100vh;
    

}
.headertxt{
    background-color:rgba(1,71,83,255);
    height:40px;
    width:200px;
    border-top-left-radius:3px;
    border-bottom-left-radius:3px;
    border-bottom-right-radius:3px;
    border-top-right-radius:5px;
    font-family: "Albert Sans";
    color:white;
    text-align: center;
    line-height: 40px;

    clip-path: polygon(0% 0%, 90% 0, 100% 17%, 100% 100%, 0% 100%);


}
.photoswrapper{
    height:600px;
    width:80%;
    display:flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
    
    gap: 10px; /* Gap between grid items */
   
}
.imagewrapper{
    position:relative;
    height:280px;
    width:200px;
}
.subname{
    position:absolute;
    right:0px;
      top:50%;


    height:30px;
    text-align: center;
line-height: 30px;
    clip-path: polygon(0% 0%, 89% 0, 100% 45%, 100% 100%, 0% 100%);
   font-size: 12px;
   font-weight: 400;
   font-family: "Albert sans";
   transform:rotate(270deg) translateY(-50%) ;
    width:150px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;

    background-color: #7ad2a2;

}
.photo{
   height:100%;
   width:100%;
}
.newwrapper{
    height:600px;
    width:100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.readinfo{
    padding:10px;
    display:flex;
    align-items:center;
    gap:5px;
    color: #96dab5;
    letter-spacing: 1px;
}
.readmore{
    font-family: "Albert sans";
    font-weight: 600;
    font-size: 15px;
    

}
.eachphoto{
    height:280px;
     align-self: center;
     justify-self:center;
    width:500px;
    display:flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.1); /* Box shadow */
    border-radius: 10px;


}
.headtext{
    font-family: "Albert Sans";
padding: 5px;
    font-weight: 500;
    font-size: 19px;
}
.subtext{
    color:grey;
    font-size:12px ;
    padding: 5px;

    font-weight: 700;
    letter-spacing:1px;
}
.subpart{
    width:280px;
    height:260px;
    padding: 10px;
}
.text{
    padding: 5px;
    font-family: "Albert Sans";
    font-size:16px;
}
.headerwrapper{
    width:100%;
        height:120px;
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
    

        


}

.latest{
    font-size:32px;
    font-weight: 600;

}


@media (max-width:1300px){
    .eachphoto{
       width:450px;
    }
}

@media (max-width:1200px )and (min-width:1050px){
    .eachphoto{
        width:400px;
        height:250px;

    }
    .subpart{
        height:250px;

    }
    .headtext{
        font-size: 16px;
    }
    .text{
        font-size: 14px;
    }
    .readmore{
        font-size: 14px;
    }
    .imagewrapper{
        height:250px;
    }
    
}


